module.exports = "* {\r\n      box-sizing: border-box;\r\n      padding: 0px;\r\n    }\r\n\r\n    /* Slideshow container */\r\n\r\n    .slideshow-container {\r\n      height: 100%;\r\n      position: absolute;\r\n      display: block;\r\n      margin: auto;\r\n      z-index: 1;\r\n      background-size: cover;\r\n    }\r\n\r\n    .backgrounds{\r\n      /* height: 100%; */\r\n      /* 670px */\r\n      width: 1020px;\r\n    }\r\n\r\n    #login_button {\r\n      position: absolute;\r\n      margin-left: 100px;\r\n      margin-top: 179px;\r\n      z-index: 2;\r\n      height: 75px;\r\n    }\r\n\r\n    #register_button {\r\n      position: absolute;\r\n      margin-left: 100px;\r\n      margin-top: 377px;\r\n      z-index: 2;\r\n      height: 75px;\r\n    }\r\n\r\n    #title {\r\n      position: absolute;\r\n      margin-left: 271px;\r\n      margin-top: 550px;\r\n      z-index: 2;\r\n      height: 100px;\r\n    }\r\n\r\n    /* Hide the images by default */\r\n\r\n    .mySlides {\r\n      display: none;\r\n    }\r\n\r\n    /* Next & previous buttons */\r\n\r\n    .prev,\r\n    .next {\r\n      cursor: pointer;\r\n      position: absolute;\r\n      top: 50%;\r\n      width: auto;\r\n      margin-top: -22px;\r\n      padding: 16px;\r\n      color: white;\r\n      font-weight: bold;\r\n      font-size: 18px;\r\n      transition: 0.6s ease;\r\n      border-radius: 0 3px 3px 0;\r\n    }\r\n\r\n    /* Position the \"next button\" to the right */\r\n\r\n    .next {\r\n      right: 0;\r\n      border-radius: 3px 0 0 3px;\r\n    }\r\n\r\n    /* On hover, add a black background color with a little bit see-through */\r\n\r\n    .prev:hover,\r\n    .next:hover {\r\n      background-color: rgba(0, 0, 0, 0.8);\r\n    }\r\n\r\n    /* Caption text */\r\n\r\n    .text {\r\n      color: #f2f2f200;\r\n      font-size: 15px;\r\n      padding: 8px 12px;\r\n      position: absolute;\r\n      bottom: 8px;\r\n      width: 100%;\r\n      text-align: center;\r\n    }\r\n\r\n    /* Number text (1